










import { Vue, Component, Prop } from 'vue-property-decorator';
import Icon from './svg-icon.vue';

@Component({
    name: 'deviceframe',
    components: {
        Icon,
    }
})
export default class DeviceFrame extends Vue {
    get deviceIsIpad() {
        return navigator.userAgent.indexOf("iPhone;") === -1;
    }
}
